import React, {useEffect, useMemo, useState} from 'react';
import Select from "react-select";
import s from './Modal.module.css';
import {postSklad1to2Action, postSklad1to3Action, postSklad1to4Action} from "../../store/actions/sklad1Actions";
import {getWorkersAction} from "../../store/actions/workersActions";
import {getOperationsAction} from "../../store/actions/operationsAction";
import {connect} from "react-redux";

const Modal = ({
                   isVisible, setIsVisible, modalData, sklad1, rozxidToSklad2, rozxidToSklad3, rozxidToSklad4, workers, operations, getOperations, getWorkers
               }) => {

    useEffect(() => {
        (async () => {
            await getOperations();
            await getWorkers();
        })();
    }, []);

    const variables = [
        {
            label: "Ð¡ÐºÐ»Ð°Ð´ 2",
            value: 'ÑÐºÐ»Ð°Ð´2',
        },
        {
            label: "Ð¡ÐºÐ»Ð°Ð´ 3",
            value: 'ÑÐºÐ»Ð°Ð´3',
        },
        {
            label: "Ð¡ÐºÐ»Ð°Ð´ 4",
            value: 'ÑÐºÐ»Ð°Ð´4',
        }
    ];

    const [selected, setSelected] = useState();
    const [dataToSubmit, setDataToSubmit] = useState({mishok: modalData, skladId: sklad1});

    const handleSelected = (e) => {
        if (e.value === "ÑÐºÐ»Ð°Ð´2") {
            setSelected(2)
            setDataToSubmit({...dataToSubmit, mishok: modalData, skladId: sklad1})
        }
        if (e.value === "ÑÐºÐ»Ð°Ð´3") {
            setSelected(3)
            setDataToSubmit({...dataToSubmit, mishok: modalData, skladId: sklad1})
        }
        if (e.value === "ÑÐºÐ»Ð°Ð´4") {
            setSelected(4)
            setDataToSubmit({...dataToSubmit, mishok: modalData, skladId: sklad1})
        }
    }

    const handleSubmit = () => {
        if (selected === 2) {
            if (dataToSubmit.mishok && dataToSubmit.shveyaId && dataToSubmit.sortId && dataToSubmit.date_rozsxodu) {
                rozxidToSklad2(dataToSubmit).then(res => res && setIsVisible(false));
                setSelected(0)
            } else {
                alert('Ð—Ð°Ð¿Ð¾Ð²Ð½Ñ–Ñ‚ÑŒ Ð²ÑÑ– Ð´Ð°Ð½Ñ– !!!')
            }
        }
        if (selected === 3) {
            if (dataToSubmit.mishok && dataToSubmit.formId && dataToSubmit.date_rozsxodu) {
                rozxidToSklad3(dataToSubmit).then(res => res && setIsVisible(false));
                setSelected(0)
            } else {
                alert('Ð—Ð°Ð¿Ð¾Ð²Ð½Ñ–Ñ‚ÑŒ Ð²ÑÑ– Ð´Ð°Ð½Ñ– !!!')
            }
        }
        if (selected === 4) {
            if (dataToSubmit.mishok && dataToSubmit.packId && dataToSubmit.date_rozsxodu) {
                rozxidToSklad4(dataToSubmit).then(res => res && setIsVisible(false));
                setSelected(0)
            } else {
                alert('Ð—Ð°Ð¿Ð¾Ð²Ð½Ñ–Ñ‚ÑŒ Ð²ÑÑ– Ð´Ð°Ð½Ñ– !!!')
            }
        }
    };
    const handleDate = ({target}) => {
        setDataToSubmit({...dataToSubmit, date_rozsxodu: target.value})
    };
    const handleForm = (e) => {
        setDataToSubmit({...dataToSubmit, formId: e.value})
    };
    const handlePack = (e) => {
        setDataToSubmit({...dataToSubmit, packId: e.value})
    };
    const handleShveya = (e) => {
        setDataToSubmit({...dataToSubmit, shveyaId: e.value})
    };
    const handleSort = (e) => {
        setDataToSubmit({...dataToSubmit, sortId: e.value})
    };

    const operationsObject = useMemo(() => {
        const temp = {};
        operations.forEach((operation) => {
            workers.forEach((worker) => {
                if (!temp[operation.name]) {
                    temp[operation.name] = [worker];
                    return;
                }
                temp[operation.name].push(worker);
            });
        });
        return temp;
    }, [workers, operations]);

    const operationsOptions = useMemo(() => {
        const temp = {};
        Object.entries(operationsObject).forEach(([key, value]) => {
            value.forEach((operation) => {
                const isCorrect = !!operation.operationId.find(({name}) => {
                    return name === key;
                });
                if (!isCorrect) return;
                const {name, fName, sName} = operation;
                const option = {label: `${fName} ${sName}`, value: operation._id};
                if (!temp[key]) {
                    temp[key] = [option];
                    return;
                }
                temp[key].push(option);
            });
        });
        return temp;
    }, [operationsObject]);

    const handleExit = () => {
        setIsVisible(false)
        setSelected(0)
    }

    return isVisible && (
        <div className={s.container}>
            <div className={s.sub_container}>
                <div className={s.header}>
                    <p>Ð Ð¾Ð·Ñ…Ñ–Ð´ Ð½Ð° </p>
                    <p className={s.exit} onClick={handleExit}>X</p>
                </div>
                <Select
                    defaultValue={variables[0].label}
                    options={variables}
                    onChange={handleSelected}
                />
                {selected === 2
                && <div className={s.sklad2}>
                    <div className={s.select}>
                        <p>Ð’Ð¸Ð±ÐµÑ€Ñ–Ñ‚ÑŒ ÑˆÐ²ÐµÑŽ</p>
                        <Select
                            defaultValue={variables[0].label}
                            options={operationsOptions["Ð¨Ð²ÐµÑ"]}
                            onChange={handleShveya}
                        />
                        <p>Ð’Ð¸Ð±ÐµÑ€Ñ–Ñ‚ÑŒ ÑÐ¾Ñ€Ñ‚ÑƒÐ²Ð°Ð»ÑŒÐ½Ð¸Ñ†ÑŽ</p>
                        <Select
                            defaultValue={variables[0].label}
                            options={operationsOptions["Ð¡Ð¾Ñ€Ñ‚ÑƒÐ²Ð°Ð»ÑŒÐ½Ð¸Ñ†Ñ"]}
                            onChange={handleSort}
                        />
                    </div>
                    <label className={s.data}>
                        <p>Ð’Ð¸Ð±ÐµÑ€Ñ–Ñ‚ÑŒ Ð´Ð°Ñ‚Ñƒ Ñ€Ð¾Ð·Ñ…Ð¾Ð´Ñƒ</p>
                        <input type={'date'} onChange={handleDate}/>
                    </label>
                </div>}


                {selected === 3 &&
                <div className={s.sklad2}>
                    <div className={s.select}>
                        <p>Ð’Ð¸Ð±ÐµÑ€Ñ–Ñ‚ÑŒ Ð¤Ð¾Ñ€Ð¼ÑƒÐ²Ð°Ð»ÑŒÐ½Ð¸Ñ†ÑŽ</p>
                        <Select
                            defaultValue={variables[0].label}
                            options={operationsOptions["Ð¤Ð¾Ñ€Ð¼ÑƒÐ²Ð°Ð»ÑŒÐ½Ð¸Ñ†Ñ"]}
                            onChange={handleForm}
                        />
                    </div>
                    <label className={s.data}>
                        <p>Ð’Ð¸Ð±ÐµÑ€Ñ–Ñ‚ÑŒ Ð´Ð°Ñ‚Ñƒ Ñ€Ð¾Ð·Ñ…Ð¾Ð´Ñƒ</p>
                        <input type={'date'} onChange={handleDate}/>
                    </label>
                </div>
                }

                {selected === 4 && <div className={s.sklad2}>
                    <div className={s.select}>
                        <p>Ð’Ð¸Ð±ÐµÑ€Ñ–Ñ‚ÑŒ Ð¿Ð°ÐºÑƒÐ²Ð°Ð»ÑŒÐ½Ð¸Ñ†Ñ–</p>
                        <Select
                            defaultValue={variables[0].label}
                            options={operationsOptions["ÐŸÐ°ÐºÑƒÐ²Ð°Ð»ÑŒÐ½Ð¸Ñ†Ñ"]}
                            onChange={handlePack}
                        />
                    </div>
                    <label className={s.data}>
                        <p>Ð’Ð¸Ð±ÐµÑ€Ñ–Ñ‚ÑŒ Ð´Ð°Ñ‚Ñƒ Ñ€Ð¾Ð·Ñ…Ð¾Ð´Ñƒ</p>
                        <input type={'date'} onChange={handleDate}/>
                    </label>
                </div>}


                <div className={s.footer}>
                    <button className={s.btn} onClick={handleSubmit}>
                        {"Ð—Ñ€Ð¾Ð±Ð¸Ñ‚Ð¸ Ñ€Ð¾Ð·Ñ…Ñ–Ð´ Ð½Ð° " + (variables[selected - 2]?.label || "")}
                    </button>
                </div>

            </div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        workers: state.workers.workers,
        operations: state.operations.operations
    };
};
const mapDispatchToProps = (dispatch) => {
    return {
        rozxidToSklad2: (data) => dispatch(postSklad1to2Action(data)),
        rozxidToSklad3: (data) => dispatch(postSklad1to3Action(data)),
        rozxidToSklad4: (data) => dispatch(postSklad1to4Action(data)),
        getWorkers: () => dispatch(getWorkersAction()),
        getOperations: () => dispatch(getOperationsAction())
    };
};
export default connect(mapStateToProps, mapDispatchToProps)(Modal);
